<template>
  <div class="z-50" id="contact">
    <section
      class="flex flex-col justify-end items-end"
      :class="containerClass"
    >
      <p
        @click="showFrom()"
        :class="[dark ? 'bg-white' : 'bg-dark-black']"
        class="p-2 max-w-max rounded-full shadow hover:shadow-md cursor-pointer border hover:opacity-90 z-10"
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          :stroke="[dark ? 'black' : 'white']"
          :fill="[dark ? 'black' : 'white']"
          viewBox="0 0 32 32"
        >
          <path
            d="M 16 5 C 12.144531 5 9 8.144531 9 12 C 9 14.410156 10.230469 16.550781 12.09375 17.8125 C 8.527344 19.34375 6 22.882813 6 27 L 8 27 C 8 24.109375 9.527344 21.59375 11.8125 20.1875 C 12.484375 21.835938 14.121094 23 16 23 C 17.878906 23 19.515625 21.835938 20.1875 20.1875 C 22.472656 21.59375 24 24.109375 24 27 L 26 27 C 26 22.882813 23.472656 19.34375 19.90625 17.8125 C 21.769531 16.550781 23 14.410156 23 12 C 23 8.144531 19.855469 5 16 5 Z M 16 7 C 18.773438 7 21 9.226563 21 12 C 21 14.773438 18.773438 17 16 17 C 13.226563 17 11 14.773438 11 12 C 11 9.226563 13.226563 7 16 7 Z M 16 19 C 16.820313 19 17.601563 19.117188 18.34375 19.34375 C 17.996094 20.308594 17.089844 21 16 21 C 14.910156 21 14.003906 20.308594 13.65625 19.34375 C 14.398438 19.117188 15.179688 19 16 19 Z"
          />
        </svg>
      </p>
      <div
        class="text-black rounded-md -translate-y-14 rounded-tr-4xl border border-gray-200 dark:border-gray-700 mt-1 dark:bg-dark-black w-104 bg-white p-4 transform z-0"
        :class="[
          formShow ? 'clip-active visible shadow-md' : 'clip-remove invisible',
        ]"
        id="form"
      >
        <h1
          class="pb-3 border-b dark:text-white text-lg lg:text-base border-gray-100 font-semibold transform"
        >
          Contact Me
        </h1>
        <form class="mt-10" :class="formClass" method="POST">
          <div class="flex">
            <div class="flex-1 mr-2">
              <input
                id="name"
                type="text"
                name="name"
                autocomplete="off"
                class="block py-2 px-1 border-b-2 w-full rounded"
                :class="[
                  dark
                    ? 'bg-gray-500 border-gray-300 text-white'
                    : 'border-gray-300 text-gray-800 bg-gray-50',
                ]"
              />
              <label for="name" class="">
                Name
              </label>
            </div>
            <div class="flex-1 ml-2">
              <input
                id="email"
                type="email"
                name="email"
                autocomplete="off"
                :class="[
                  dark
                    ? 'bg-gray-500 border-gray-300 text-white'
                    : 'border-gray-300 text-gray-800 bg-gray-50',
                ]"
                class="block w-full py-2 px-1 border-b-2 rounded"
              />
              <label for="email" class="">
                E-mail
              </label>
            </div>
          </div>
          <div :class="[dark ? 'white-label' : '']" class="mt-10">
            <textarea
              style="max-height: 140px; min-height: 120px;"
              name="msg"
              id="msg"
              :class="[
                dark
                  ? 'bg-gray-500 border-gray-300 text-white'
                  : 'border-gray-300 text-gray-800 bg-gray-50',
              ]"
              class="block w-full py-2 px-1 border-b-2 rounded"
            ></textarea>
            <label for="msg" class="">
              Message
            </label>
          </div>
          <button
            type="submit"
            class="py-2 px-6 mt-10 rounded font-medium uppercase focus:outline-none hover:shadow"
            :class="[
              dark
                ? 'hover:bg-gray-300 bg-white text-dark-black'
                : 'hover:bg-gray-700 text-white bg-dark-black',
            ]"
          >
            Send
          </button>
        </form>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  data() {
    return {
      formShow: false,
      formClass: '',
      containerClass: '',
    }
  },
  mounted() {
    this.containerClass = 'w-16'
  },
  methods: {
    showFrom() {
      this.formShow = !this.formShow
      if (this.formShow) {
        return (this.formClass = 'block'), (this.containerClass = '')
      } else {
        setTimeout(() => {
          return (this.formClass = 'hidden'), (this.containerClass = 'w-16')
        }, 300)
      }
    },
  },
}
</script>

<style lang="scss" scoped>
.w-104 {
  width: 32rem;
}
.rounded-tr-4xl {
  border-top-right-radius: 1.9rem;
}
.dark {
  #contact {
    #form {
      input:focus,
      textarea:focus {
        outline: none;
        border-bottom: 3px solid rgb(250, 242, 242);
      }
      input:focus + label {
        color: rgb(246, 246, 246);
      }
      textarea:focus + label {
        color: rgb(254, 254, 254);
      }
    }
  }
}
#contact {
  #form {
    transition: all 0.3s linear;
    label {
      transition: transform 0.3s;
      position: absolute;
      transform: translate(10px, -30px);
      color: rgb(199, 199, 199);
    }
    input:focus,
    textarea:focus {
      outline: none;
      border-bottom: 3px solid black;
    }
    input:focus + label {
      transform: translateY(-67px);
      color: rgb(34, 32, 32);
    }
    textarea:focus + label {
      transform: translateY(-148px);
      color: rgb(34, 32, 32);
    }
  }
  .clip-active {
    clip-path: circle(100% at 25% 40%);
  }
  .clip-remove {
    clip-path: circle(8% at 94% 8%);
  }
  position: fixed;
  right: 30px;
  top: 18px;
  p {
    transition: all 0.2s linear;
    svg {
      transition: all 0.2s linear;
      width: 2rem;
      height: 2rem;
    }
  }
}
@media (max-width: 1024px) {
  .w-104 {
    width: 24rem;
  }
}
@media (max-width: 768px) {
  #contact {
    right: 12px;
    top: 10px;
  }
}
@media (max-width: 480px) {
  .w-104 {
    width: 100%;
  }
  #contact {
    right: 8px;
    top: 8px;
    p {
      svg {
        width: 1.6rem;
        height: 1.6rem;
      }
    }
  }
}
</style>
