<template>
  <div
    v-if="loader"
    class="w-full h-screen top-0 right-0 bottom-0 left-0 fixed z-index-full overflow-hidden"
  >
    <div
      class="relative bg-white dark:bg-dark-black h-full flex items-center justify-center"
    >
      <div class="spinner-box">
        <div class="configure-border-1 relative">
          <div class="configure-core"></div>
          <div class="configure-border-2">
            <div class="configure-core"></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  beforeMount() {
    const time = Math.round(performance.now())
    setTimeout(() => {
      return (this.loader = false)
    }, time)
  },
  data() {
    return {
      loader: true,
    }
  },
}
</script>

<style lang="scss">
.z-index-full {
  z-index: 999999999999999;
  scrollbar-width: 0px;
}
@keyframes configure-clockwise {
  0% {
    transform: rotate(0);
    border-radius: 0%;
    padding: 2px;
  }
  25% {
    transform: rotate(90deg);
    border-radius: 25%;
    padding: 2.5px;
  }
  50% {
    transform: rotate(180deg);
    border-radius: 50%;
    padding: 3px;
  }
  75% {
    transform: rotate(270deg);
    border-radius: 75%;
    padding: 3.5px;
  }
  100% {
    transform: rotate(2turn);
    padding: 2px;
    box-shadow: 0px 0px 10px 5px rgba(255, 129, 97, 0.1);
  }
}
@keyframes configure-xclockwise {
  0% {
    transform: rotate(45deg);
    border-radius: 0%;
  }
  25% {
    transform: rotate(-45deg);
    border-radius: 25%;
  }
  50% {
    transform: rotate(45deg);
    border-radius: 50%;
  }
  75% {
    transform: rotate(1turn);
    border-radius: 75%;
  }
  100% {
    transform: rotate(-315deg);
    border-radius: 100%;
  }
}
.configure-border-1 {
  width: 160px;
  height: 160px;
  background: #fb5b53;
  animation: configure-clockwise 2s ease-in-out 0s infinite alternate;
}
.configure-border-2 {
  width: 80px;
  height: 80px;
  padding: 3px;
  top: 25%;
  left: 25%;
  position: absolute;
  background: #303030;
  animation: configure-xclockwise 2s ease-in-out 0s infinite alternate;
}
.dark {
  .configure-core {
    background-color: rgba(39, 37, 37, 1);
  }
  .configure-border-1 {
    background: #42b983;
  }
  .configure-border-2 {
    background: #ffffff;
  }
}
.configure-core {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}
</style>
